<template>
	<div>
		<!-- <span id="date">Today</span> -->
	<el-row :gutter="12">
      <el-col :span="16" :offset="4" v-for="(item,index) in Array.prototype.reverse.call(detail)">
        <el-card shadow="hover" class="entryCard">
          <div v-if="item.mood == 5" class="colorBar Orange">{{item.timestamp | getDay}}</div>
          <div v-if="item.mood == 4" class="colorBar Green">{{item.timestamp | getDay}}</div>
          <div v-if="item.mood == 3" class="colorBar Blue">{{item.timestamp | getDay}}</div>
          <div v-if="item.mood == 2" class="colorBar Grey">{{item.timestamp | getDay}}</div>
          <div v-if="item.mood == 1" class="colorBar Black">{{item.timestamp | getDay}}</div>
          <div class="emojiAudio">
            <img v-if="item.mood == 5" src="@/assets/emoji/emoji_verygood.png">
            <img v-if="item.mood == 4" src="@/assets/emoji/emoji_good.png">
            <img v-if="item.mood == 3" src="@/assets/emoji/emoji_neutral.png">
            <img v-if="item.mood == 2" src="@/assets/emoji/emoji_bad.png">
            <img v-if="item.mood == 1" src="@/assets/emoji/emoji_verybad.png">
          </div>
          <div class="detailText">
            <div class="entryWords">
              <div class='time'>{{item.timestamp | getTime}}</div>
              <div class="emotionClient">{{item.emotion_client}}</div>
              <div v-if="item.keyWords != null" class="keywords"><span>Keywords</span> {{item.keyWords}}</div>
              <div v-if="item.cognitive != null" class="distortions"><span>Distortions</span> {{item.cognitive}}</div>
            </div>
            <div class="text"> 
              {{item.text}}
            </div>
          </div>
          <div class="audioImg">
          <audio v-if="item.src != null " :src="item.src" controls></audio>
              <img v-if="item.notes != null " :src="item.notes">
          </div>
        </el-card>
      </el-col>
      <!-- <el-col :span="10">
        <el-card shadow="hover">
          Note 1
        </el-card>
        <el-card shadow="hover">
          Note 2
        </el-card>
      </el-col> -->
    </el-row>
	</div>
</template>

<script>
  import axios from 'axios'
  export default {
    data() {
      return {
        detail: '',
      }
    },
    method: {
      color(mood) {
        if (mood == 1) {
          return 'black';
        } else if (mood == 2) {
          return 'grey';
        } else if (mood == 3) {
          return 'blue';
        } else if (mood == 4) {
          return 'green';
        } else {
          return 'orange';
        }
      }
    },
    mounted() {
      var get_path = 'http://35.197.160.94:8094/patient/patientInfo?uidPatient=' + this.$store.state.patient_id
      axios.get(get_path)
      .then(response=>{
         console.log(response.data);
         this.detail=response.data;
         this.detail.forEach(el => {
          if (el.src != null) {
            var src_index = el.src.split('/').length
            el.src = '../music/' + el.src.split('/')[src_index - 1]
          }
          if (el.notes != null) {
            var note_index = el.notes.split('/').length
            el.notes = '../img/' + el.notes.split('/')[note_index - 1]
          }
          
         })
      })
      .catch(error=>{
          console.log(error);
          this.$message({
            message: "Network Error",
            type: "error"
          })
      })
    },
    filters: {
      getDay: function (value) {
        if (!value) return ''
        value = value.split('T')[0]
        return value
      },
      getTime: function (value) {
        if (!value) return ''
        value = value.split('T')[1].split('.')[0]
        return value
      },
      getSrc: function (value) {
        if (!value) return ''
        const index = value.split('/').length
        value = '../music' + value.split('/')[index - 1]
      }
    },
  }
</script>

